import React from 'react';
import { useSelector } from 'react-redux';
import { Header2 } from 'components/typography';
import Wrap from 'components/wrap';
import Project from 'components/project';
import { CustomLink } from 'components/link';

import useStyles from './use-styles';

export default function Projects() {
	const styles = useStyles();
	const { projects } = useSelector((state) => state.ProjectAll);

	return (
		<section className={styles.projects}>
			<Wrap>
				<Header2 className={styles.topHeader} title='Clients' />
				<div className={styles.projectList}>
					{projects.map((item) => (
						<Project
							key={item._id}
							title={item.project}
							description={item.description}
							sector={item.sector.name}
							type={item.type}
							client={item.client.name}
							stack={item.technologies}
							url={item.imageKeys.main}
							path={item.slug}
							logo={item.client.logoKey}
							team={item.team}
						/>
					))}
				</div>
				<div className={styles.bottomLink}>
					<CustomLink title='Show all projects' url='/projects' />
				</div>
			</Wrap>
		</section>
	);
}
