import React, { useState, useEffect, useCallback } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import useStyles from './use-styles';
import { useMediaQuery } from 'react-responsive';
import { mobileSize, tabletSize } from 'helpers/breakpoints';
import Carousel from 'components/carousel';
import Wrap from 'components/wrap';
import { Header1 } from 'components/typography';
import { CrewCard } from 'components/cards';
import { getTeamAll } from 'actions/team';

const CrewCarousel = () => {
	const styles = useStyles();
	const dispatch = useDispatch();
	const { team } = useSelector((state) => state.TeamAll);

	const isMobile = useMediaQuery(mobileSize);
	const isTablet = useMediaQuery(tabletSize);

	const slidesToShow = isMobile ? 1 : isTablet ? 2 : 4;

	const [width, setWidth] = useState(false);

	const fetchTeamAll = useCallback(() => dispatch(getTeamAll()), [dispatch]);

	useEffect(() => {
		setWidth(isMobile);
		fetchTeamAll();
	}, []);

	return (
		<section className={styles.crewCarousel}>
			<Wrap>
				<Header1 title='Other project managers in our company' />
				<Carousel
					slidesToShow={slidesToShow}
					slidesToScroll={1}
					variableWidth={width}
					className={styles.customCarousel}>
					{team.map((item, index) => {
						return (
							<CrewCard
								name={item.name}
								bio={item.roles}
								photos={[item.imageKeys.professional, item.imageKeys.smiley]}
								url={item.slug}
								index={index}
								key={item._id}
							/>
						);
					})}
				</Carousel>
			</Wrap>
		</section>
	);
};

export default CrewCarousel;
